import React, { Component } from 'react';

export default function({children}) {
  return <a target={encodeURIComponent(children)} href={`https://www.google.com/search?q=${encodeURIComponent(children)}`}>{children}</a>
}

export const H3Link = function({children}) {
  return <h3>□ <a target={encodeURIComponent(children)} href={`https://www.google.com/search?q=${encodeURIComponent(children)}`}>{children}</a></h3>
}
