import React, { Component } from 'react';
import MathJax from './MathJax';
import Link, { H3Link } from './Link';

export default class PageContainer extends Component {
  constructor(props) {
    super(props);
  }

  componentDidMount () {
  }

  componentWillUnmount() {
  }

  componentWillReceiveProps(nextProps) {
  }

  render() {
    return (
      <div>
        <div className="Page-header">
          <p className="Page-title" style={{ flex: 'auto' }}>{ this.props.title }</p>
          {
            (this.props.maxPage>0)?<p className="Page-Number" style={{ flex: 'auto' }}>{ this.props.page } / { this.props.maxPage }</p>:null
          }
        </div>
        <div className="Page-body">
          { this.props.data.split('\n').map( v => {
              const t = v.trim().match('([#&\$\!/]*)(.+)');
              if (t) {
                if (t[1] == '!') {
                  return <p style={{marginLeft: '1em'}}>{t[2].trim()}</p>
                } else
                if (t[1] == '#') {
                  return <H3Link>{t[2].trim()}</H3Link>
                } else
                if (t[1][0] == '$') {
                  return <MathJax value={v} />
                } else
                if (t[1][0] == '/') {
                  return null;
                } else
                if (t[1] == '&') {
                  const q = v.match('(&+)\\[(.+)\\](.+)')
                  return <p><a href={`${q[3].trim()}`} >{q[2]}</a></p>
                } else {
                  const q = t[2].trim().split('、');
                  return q.map( (v,i) => <span><Link>{v}</Link>{(i<q.length-1)?'、':''}</span> );
                }
              }
              return null;
            })
          }
          { this.props.children }
        </div>
      </div>
    );
  }
}

PageContainer.defaultProps = {
  title: 'title',
  page: 0,
  maxPage: 0,
  data: '',
}
